'use client'

import Link from "next/link";
import { Button } from "@/components/ui/button";
import { ThemeToggleSimple } from "@/components/ui/theme-toggle";
import { Menu, School, X, Sparkles } from "lucide-react";
import { useState, useEffect } from "react";

export function Navbar() {
  const [mobileMenuOpen, setMobileMenuOpen] = useState(false);
  const [scrolled, setScrolled] = useState(false);

  useEffect(() => {
    const handleScroll = () => {
      setScrolled(window.scrollY > 20);
    };
    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, []);

  return (
    <header className={`sticky top-0 z-50 w-full transition-all duration-300 ${
      scrolled 
        ? 'glass border-b border-white/10 shadow-lg' 
        : 'bg-transparent'
    }`}>
      <div className="container mx-auto flex h-16 items-center justify-between px-4">
        {/* Logo */}
        <div className="flex items-center gap-2">
          <Link href="/" className="flex items-center space-x-3 group">
            <div className="relative w-10 h-10 bg-gradient-to-br from-primary to-secondary rounded-xl flex items-center justify-center shadow-lg group-hover:shadow-primary/30 transition-all duration-300 group-hover:scale-105">
              <School className="h-5 w-5 text-white" />
              <div className="absolute inset-0 bg-white/20 rounded-xl opacity-0 group-hover:opacity-100 transition-opacity duration-300" />
            </div>
            <span className="text-xl font-bold font-heading gradient-text">
              Institute SaaS
            </span>
          </Link>
        </div>

        {/* Desktop Navigation */}
        <nav className="hidden md:flex items-center gap-1">
          {['Home', 'Features', 'Pricing', 'About', 'Contact'].map((item, index) => (
            <Link 
              key={item}
              href={item === 'Home' ? '/' : `/${item.toLowerCase()}`}
              className="relative px-4 py-2 text-sm font-medium text-foreground/80 hover:text-foreground transition-colors duration-200 group"
              style={{ animationDelay: `${index * 0.1}s` }}
            >
              {item}
              <span className="absolute bottom-0 left-1/2 -translate-x-1/2 w-0 h-0.5 bg-gradient-to-r from-primary to-secondary group-hover:w-3/4 transition-all duration-300" />
            </Link>
          ))}
        </nav>

        {/* Auth Buttons & Theme Toggle */}
        <div className="hidden md:flex items-center gap-3">
          <ThemeToggleSimple />
          <Button variant="ghost" className="glass-button" asChild>
            <Link href="/auth/login">Log In</Link>
          </Button>
          <Button className="relative overflow-hidden group" asChild>
            <Link href="/auth/signup">
              <span className="relative z-10 flex items-center gap-2">
                <Sparkles className="h-4 w-4 animate-pulse" />
                Get Started
              </span>
              <span className="absolute inset-0 bg-gradient-to-r from-primary via-secondary to-primary bg-[length:200%_100%] animate-shimmer opacity-0 group-hover:opacity-100 transition-opacity duration-300" />
            </Link>
          </Button>
        </div>

        {/* Mobile Menu Toggle */}
        <div className="md:hidden flex items-center gap-2">
          <ThemeToggleSimple />
          <Button 
            variant="ghost" 
            size="icon"
            className="glass-button"
            onClick={() => setMobileMenuOpen(!mobileMenuOpen)}
          >
            <div className="relative w-5 h-5">
              <Menu className={`absolute inset-0 h-5 w-5 transition-all duration-300 ${
                mobileMenuOpen ? 'rotate-90 scale-0 opacity-0' : 'rotate-0 scale-100 opacity-100'
              }`} />
              <X className={`absolute inset-0 h-5 w-5 transition-all duration-300 ${
                mobileMenuOpen ? 'rotate-0 scale-100 opacity-100' : '-rotate-90 scale-0 opacity-0'
              }`} />
            </div>
          </Button>
        </div>
      </div>

      {/* Mobile Menu */}
      <div className={`md:hidden overflow-hidden transition-all duration-300 ease-in-out ${
        mobileMenuOpen ? 'max-h-96 opacity-100' : 'max-h-0 opacity-0'
      }`}>
        <div className="glass border-t border-white/10 px-4 py-4 space-y-2">
          {['Home', 'Features', 'Pricing', 'About', 'Contact'].map((item, index) => (
            <Link 
              key={item}
              href={item === 'Home' ? '/' : `/${item.toLowerCase()}`}
              className={`block px-4 py-3 rounded-xl text-foreground/80 hover:text-foreground hover:bg-white/10 dark:hover:bg-white/5 transition-all duration-200 animate-slide-up`}
              style={{ animationDelay: `${index * 0.05}s` }}
              onClick={() => setMobileMenuOpen(false)}
            >
              {item}
            </Link>
          ))}
          <div className="pt-4 border-t border-white/10 space-y-2">
            <Button variant="ghost" className="w-full glass-button justify-center" asChild>
              <Link href="/auth/login" onClick={() => setMobileMenuOpen(false)}>
                Log In
              </Link>
            </Button>
            <Button className="w-full" asChild>
              <Link href="/auth/signup" onClick={() => setMobileMenuOpen(false)}>
                <Sparkles className="h-4 w-4 mr-2" />
                Get Started
              </Link>
            </Button>
          </div>
        </div>
      </div>
    </header>
  );
}
